import React, { FC } from 'react';
import dayjs from 'dayjs';
import { Row, Col } from 'reactstrap';
import FontAwesome from 'react-fontawesome';
import Gravatar from 'react-gravatar'

interface Props{
  init:any
}

const Header:FC<Props> = ({init}) =>{
  const {
    title, bride, groom, wedding, email
  } = init;
  dayjs.locale('ko');  
  return (
    <Row>
      <Col>
        <Row>
          <Col className="app-fluid">
            <div className="app-header d-flex justify-content-center align-items-center">
              <h2 className="mb-0">{title}</h2>
            </div>
            <img className="img-fluid" src='https://picsum.photos/id/1018/1000/600/' alt="header" />
            {/* <Gravatar email={email}/> */}
          </Col>
        </Row>
        <Row>
          <Col className="pt-5">
            <h3>
              {bride.name}&nbsp;
              <small>
                <span className="text-middle" style={{ color: 'red' }}>
                  <FontAwesome name="heart" />
                </span>
              </small>
              &nbsp;{groom.name}
            </h3>
            <h3>Invite Wedding Cereminies</h3>            
            <p className="lead pt-3">{dayjs(wedding.at).format('YYYY-MM-DD HH:mm')}</p>
            <p className="lead">{wedding.name}</p>
          </Col>
        </Row>
      </Col>
    </Row>
  );
}

export default Header;