import React, { useState } from 'react';
import CommentsBlock from 'simple-react-comments';

interface CommentEntity{
    avatarUrl: string;
    authorUrl: string;
    fullName: string;
    createdAt: Date;
    text: string;
}

const Comments = () =>{
    
    const tempDate = [{
        avatarUrl: "https://picsum.photos/id/1018/250/150/",
        authorUrl: "https://picsum.photos/id/1018/250/150/",
        fullName: "송중기",
        createdAt: new Date(),
        text: "Comment Test",
    }]
    const initDate = {
        comments: tempDate,
      };
    const [comments, setComments] = useState(initDate);
    return (        
          <CommentsBlock
            comments={comments.comments}
            signinUrl={'/signin'}
            isLoggedIn
            reactRouter={false}            
            onSubmit={text => {
              if (text.length > 0) {
                setComments({
                comments: [
                  ...comments.comments,
                  {
                    authorUrl: 'https://picsum.photos/id/1019/250/150/',
                    avatarUrl: 'https://picsum.photos/id/1019/250/150/',
                    createdAt: new Date(),
                    fullName: '김수현',
                    text,
                  },
                ],
              });                
              console.log('submit:', text);
              }              
            }}
          /> 
      );
}
 
export default Comments;